import React, { FC, ReactElement } from 'react'
import { Navigate, useLocation } from 'react-router-dom'
import { isConsultant, isUser } from '../../../utils/check-auth'
import { IRequireAuthProps } from '../../../services/types/auth/auth-types'
import { useAppSelector } from '../../../services/store/hooks'
import { TRootState } from '../../../services/store/store'

const RequireVacancyAuth: FC<IRequireAuthProps> = ({ children, redirectTo = '/sign-in' }) => {
  const { isLoggedIn, role } = useAppSelector((store: TRootState) => store.auth)
  const location = useLocation()

  if (!isLoggedIn) return <Navigate to={{ pathname: redirectTo }} state={{ from: location }} />
  if (isUser(role)) return <Navigate to={{ pathname: '/' }} state={{ from: location }} />
  if (isConsultant(role)) return <Navigate to={{ pathname: '/consultant' }} state={{ from: location }} />

  return children as ReactElement
}

export default RequireVacancyAuth
