import { TUserRole } from '../services/types/auth/auth-types'
import { CONSULTANT_ROLES, VACANCY_MANAGER_ROLES, USER_ROLES, WORKSHOP_MANAGER_ROLES } from './constants'

export const isConsultant = (role: TUserRole) => role && CONSULTANT_ROLES.includes(role)
export const isVacancyManager = (role: TUserRole) => role && VACANCY_MANAGER_ROLES.includes(role)
export const isUser = (role: TUserRole) => role && USER_ROLES.includes(role)
export const isWorkshopManager = (role: TUserRole) => role && WORKSHOP_MANAGER_ROLES.includes(role)
